import React from "react";
import { observer, inject } from "mobx-react";
import { Tabs, Button, Modal, Icon } from "antd-mobile";
import SubPage from "@/components/SubPage";
import DateRangeSelect from "@/components/DateRangeSelect";
import CommonListView from "@/components/CommonListView";
import EasyNarBar from "@/components/EasyNarBar";
import dayjs from "dayjs";
require("./style.scss");

interface IState {
  [key: string]: any;
}

@inject("userInfoStore")
@inject("meetingManagementStore")
@observer
class MeetingManagement extends React.Component<IProps, IState> {
  lv: any;
  tab1: any;
  tab2: any;
  tab3: any;
  now: string;
  constructor(props: IProps) {
    super(props);
    this.state = {
      height: 0,
      visible: false
    };
    this.lv = React.createRef();
    this.tab1 = React.createRef();
    this.tab2 = React.createRef();
    this.tab3 = React.createRef();
    this.now = dayjs().format();
  }

  componentDidMount() {
    const {
      userInfoStore: { fetchUserInfo }
    } = this.props;
    fetchUserInfo();
    const barDom = document.querySelector(".am-tabs-tab-bar-wrap");
    const dateRangeDom = document.querySelector(".dateRange-select-wrap");
    const navbarDom = document.querySelector(".am-navbar");

    this.setState({
      height:
        Math.max(document.documentElement.clientHeight, document.body.clientHeight) -
        (navbarDom ? navbarDom.offsetHeight : 0) -
        (barDom ? barDom.offsetHeight : 0) -
        (dateRangeDom ? dateRangeDom.offsetHeight : 0)
    });
  }

  componentWillUnmount() {
    const {
      meetingManagementStore: { reset }
    } = this.props;
    reset();
  }

  createMeeting(type: string) {
    const { history } = this.props;
    return () => {
      if (type) {
        history.push({
          pathname: `/meeting-management/${type}/add`
          // state: { type },
        });
      }
    };
  }

  renderRow = (item: any) => (
    <div
      key={item.id}
      className="meetingList-item"
      onClick={() => {
        this.props.history.push({
          pathname: `/meeting-management/${Number(item.type) ? "outside" : "inside"}/${item.id}`
        });
      }}
    >
      <div className="meetingList-item-name">{item.name || ""}</div>
      <div className="meetingList-item-text">
        会议时间:
        {item.startTime && item.endTime ? `${dayjs(item.startTime).format("YYYY-MM-DD HH:mm")}-${dayjs(item.endTime).format("HH:mm")}` : ""}
      </div>
      <div className="meetingList-item-text">
        地点:
        {(() => {
          if (Number(item.type) === 0) {
            return item.roomName || "";
          } else {
            return item.address || "";
          }
        })()}
      </div>
      <Icon className="meetingList-item-icon" type="right" size="sm" />
    </div>
  );

  getFixedParams = () => {
    const {
      userInfoStore: { userMemberInfo }
    } = this.props;
    const { id, WorkNumber } = userMemberInfo || {};
    if (id && WorkNumber) {
      return [
        {
          type: 1,
          field: "meeting_info_wx_view.meetingSponsorId",
          condition: "=",
          connection: "&&",
          id: 1,
          pid: 0,
          value: id
        }
      ];
    } else {
      return [];
    }
  };

  /**
   * 会议开始时间：当前时间和时间选择器的开始时间的较大值，会议结束时间：选择器结束时间（如果有）
   */
  returnNotParams = () => {
    const {
      meetingManagementStore: { notStartedTimeRange }
    } = this.props;
    const [start, end] = notStartedTimeRange || [];

    return this.getFixedParams()
      .concat(
        {
          type: 1,
          field: "meeting_info_wx_view.startTime",
          condition: ">",
          connection: "&&",
          id: 8,
          pid: 0,
          value: start ? dayjs(Math.max(dayjs().valueOf(), dayjs(start).valueOf())).format() : dayjs().format()
        },
        end && {
          type: 1,
          field: "meeting_info_wx_view.endTime",
          condition: "<",
          connection: "&&",
          id: 9,
          pid: 0,
          value: dayjs(end).format()
        }
      )
      .filter((item: any) => item);
  };

  /**
   * 符合当前时间大于会议开始时间并且小于结束时间
   */
  returnProcessingParams = () => {
    const {
      meetingManagementStore: { processingTimeRange }
    } = this.props;
    const [start, end] = processingTimeRange || [];

    return this.getFixedParams().concat([
      {
        type: 1,
        field: "meeting_info_wx_view.startTime",
        condition: "<",
        connection: "&&",
        id: 8,
        pid: 0,
        value: dayjs().format()
      },
      {
        type: 1,
        field: "meeting_info_wx_view.endTime",
        condition: ">",
        connection: "&&",
        id: 9,
        pid: 0,
        value: dayjs().format()
      }
    ]);
  };

  /**
   * 会议开始时间：选择器开始时间，会议结束时间：当前时间和时间选择器的结束时间的较小值
   */
  returnOverParams = () => {
    const {
      meetingManagementStore: { overTimeRange }
    } = this.props;
    const [start, end] = overTimeRange || [];

    return this.getFixedParams()
      .concat([
        {
          type: 1,
          field: "meeting_info_wx_view.startTime",
          condition: ">",
          connection: "&&",
          id: 8,
          pid: 0,
          value: dayjs(start).format()
        },
        {
          type: 1,
          field: "meeting_info_wx_view.endTime",
          condition: "<",
          connection: "&&",
          id: 9,
          pid: 0,
          value: end ? dayjs(Math.min(dayjs().valueOf(), dayjs(end).valueOf())).format() : dayjs().format()
        }
      ])
      .filter((item: any) => item);
  };

  render() {
    const {
      history,
      userInfoStore: { userMemberInfo },
      meetingManagementStore: {
        title,
        tabs,
        curTab,
        onTabChange,
        getMeeting,
        changeNotStartedTimeRange,
        changeProcessingTimeRange,
        changeOverTimeRange,
        notStartedTimeRange_default,
        processingTimeRange_default,
        overTimeRange_default
      }
    } = this.props;
    const { height, visible } = this.state;

    return (
      <>
        <EasyNarBar
          mainContent="会议管理"
          leftContent={<Icon type="left" />}
          onLeftClick={() => {
            this.props.history.goBack();
          }}
          rightContent={
            <span
              onClick={e => {
                e.preventDefault();
                this.setState({ visible: !this.state.visible });
              }}
              style={{ fontSize: "15px" }}
            >
              创建会议
            </span>
          }
        ></EasyNarBar>

        <div ref={this.lv}>
          <Tabs tabs={tabs} page={curTab} onChange={onTabChange} animated={false} useOnPan={false} swipeable={false}>
            <div>
              <DateRangeSelect
                label="会议日期"
                defaultValue={notStartedTimeRange_default}
                onChange={(dates: any) => {
                  changeNotStartedTimeRange(dates);
                  this.tab1.current.reset();
                }}
              />
              {height && userMemberInfo && (
                <CommonListView
                  ref={this.tab1}
                  height={height}
                  fetchList={getMeeting}
                  limit={20}
                  renderRow={this.renderRow}
                  params={{ orderBy: [{ fieldName: "meeting_info_wx_view.startTime", sort: "DESC" }], filterCondition: this.returnNotParams() }}
                />
              )}
            </div>
            <div>
              <DateRangeSelect
                label="会议日期"
                defaultValue={processingTimeRange_default}
                onChange={(dates: any) => {
                  changeProcessingTimeRange(dates);
                  this.tab2.current.reset();
                }}
              />
              {height && userMemberInfo && (
                <CommonListView
                  ref={this.tab2}
                  height={height}
                  fetchList={getMeeting}
                  limit={20}
                  renderRow={this.renderRow}
                  params={{ filterCondition: this.returnProcessingParams() }}
                />
              )}
            </div>
            <div>
              <DateRangeSelect
                label="会议日期"
                defaultValue={overTimeRange_default}
                onChange={(dates: any) => {
                  changeOverTimeRange(dates);
                  this.tab3.current.reset();
                }}
              />
              {height && userMemberInfo && (
                <CommonListView
                  ref={this.tab3}
                  height={height}
                  fetchList={getMeeting}
                  limit={20}
                  renderRow={this.renderRow}
                  params={{ filterCondition: this.returnOverParams() }}
                />
              )}
            </div>
          </Tabs>
        </div>

        <Modal visible={visible} transparent onClose={() => this.setState({ visible: false })}>
          <div className="meetingManagement-modal">
            <Button type="primary" onClick={this.createMeeting("inside")}>
              自有会议
            </Button>
            <Button type="primary" onClick={this.createMeeting("outside")}>
              非自有会议
            </Button>
          </div>
        </Modal>
      </>
    );
  }
}

export default MeetingManagement;
