import React, { useState, useRef, useEffect } from "react";
import { ListView } from "antd-mobile";
require("./style.scss");
interface IState {
  [key: string]: any;
}

interface pageInfo {
  data: any[];
  total: number;
}

const Transition = ({ isLoading, hasMore, hasData }) => {
  return (
    <div className="commonListView-transition">
      {(() => {
        if (isLoading) {
          return "加载中...";
        } else if (!hasData) {
          return "暂无数据";
        } else if (hasMore) {
          return "加载完成";
        }
      })()}
    </div>
  );
};

class CommonListView extends React.Component<GProps, IState> {
  listViewRef: any;
  constructor(props: GProps) {
    super(props);
    this.state = {
      skip: 0,
      pageSize: 0,
      isLoading: false,
      hasMore: true,
      data: [],
      filterData: null,
      dataSource: new ListView.DataSource({
        rowHasChanged: (row1: any, row2: any) => row1 !== row2
      })
    };
    this.listViewRef = React.createRef();
  }

  componentDidMount() {
    this.getList();
  }

  getList() {
    const { fetchList, limit, params } = this.props;
    const { isLoading, skip } = this.state;
    if (isLoading) return;
    this.setState({ isLoading: true });
    fetchList(skip, limit, params)
      .then((pageInfo: pageInfo) => {
        const { total, data } = pageInfo;
        if (data && data.length > 0) {
          const newData = [...this.state.data, ...data];
          this.setState({
            data: newData,
            dataSource: this.state.dataSource.cloneWithRows(newData),
            hasMore: newData.length < total,
            skip: skip + limit
          });
        }
      })
      .finally(() => {
        this.setState({ isLoading: false });
      });
  }

  onEndReached = () => {
    const { isLoading, hasMore } = this.state;

    if (!isLoading && hasMore) {
      this.getList();
    }
  };

  onResultSearch = (value: string) => {
    this.setState(
      {
        skip: 0,
        isLoading: false,
        hasMore: true,
        dataSource: new ListView.DataSource({
          rowHasChanged: (row1: any, row2: any) => row1 !== row2
        })
      },
      () => {
        if (value) {
          const curData = this.state.filterData || this.state.data;
          const filterData = curData.filter((item: any) => item.name.indexOf(value) !== -1);
          this.setState(
            {
              filterData,
              dataSource: this.state.dataSource.cloneWithRows(filterData),
              pageSize: filterData.length,
              hasMore: false
            },
            () => {
              this.listViewRef.current.scrollTo(0, 0);
            }
          );
        } else {
          this.setState(
            {
              filterData: null,
              pageSize: 0
            },
            () => {
              this.getList();
            }
          );
        }
      }
    );
  };

  reset = () => {
    this.setState(
      {
        skip: 0,
        isLoading: false,
        hasMore: true,
        data: [],
        dataSource: new ListView.DataSource({
          rowHasChanged: (row1: any, row2: any) => row1 !== row2
        })
      },
      () => {
        this.listViewRef.current.scrollTo(0, 0);
        this.getList();
      }
    );
  };

  render() {
    const { height, limit, renderRow } = this.props;
    const { isLoading, hasMore, data, dataSource, pageSize } = this.state;

    return (
      <ListView
        ref={this.listViewRef}
        dataSource={dataSource}
        renderFooter={() => {
          return <Transition isLoading={isLoading} hasMore={hasMore} hasData={data.length > 0} />;
        }}
        renderRow={renderRow}
        pageSize={pageSize || limit}
        style={{ height }}
        scrollRenderAheadDistance={500}
        onEndReached={this.onEndReached}
        onEndReachedThreshold={10}
      />
    );
  }
}

export default CommonListView;
